.richtext {
    .richtext1 {
        background: white;
        border-radius: 10px;
        box-shadow: 0 0 2px 2px #ccc;
        padding: 20px;
        margin-bottom: 10px;
        p {
            font-size: 18px;
            border-bottom: 1px solid #aaa;
        }
    }
    .change {
        width: 50%;
        background: white;
        border-radius: 10px;
        box-shadow: 0 0 2px 2px #ccc;
        padding: 10px;
        p {
            font-size: 18px;
            border-bottom: 1px solid #aaa;
        }
    }
}