.home {
    height: 100%;
    .left {
        background: #001529;
        position: relative;
        height: 100%;
        overflow-y: scroll;
        &::-webkit-scrollbar {
            display: none;
        }
        &::-moz-scrollbar {
            display: none;
        }
        &::-ms-scrollbar {
            display: none;
        }
        .ant-menu {
            height: 100%;
            background: #001529;
            position: relative;
            z-index: 100;
            .h5 {
                font-size: 50px;
                margin-left: 95px;
                color: white;
                margin-bottom: 10px;
                margin-top: 16px;
            }
            .inter {
                margin-right: 25px;
            }
            .ant-menu-sub {
                a {
                    margin-left: 15px;
                }
            }
        }
    }
    .right {
        width: 100%;
        height: 100%;
        padding-left: 256px;
        position: absolute;
        top: 0;
        .header {
            width: 100%;
            height: 120px;
            background: white;
            margin-bottom: 3px;
            box-shadow: 0 0 2px 2px #ccc;
            position: relative;
            .aGithub {
                height: 24px;
                position: absolute;
                top: 25px;
                right: 150px;
                .github {
                    color: black;
                    font-size: 24px;
                }
            }
            .ant-dropdown-link {
                width: 50px;
                height: 50px;
                position: absolute;
                top: 5px;
                right: 50px;
                .ant-avatar {
                    width: 50px;
                    height: 50px;
                }
                p {
                    color: black;
                    height: 20px;
                    font-size: 12px;
                    text-align: center;
                    overflow: hidden; 
                    white-space: nowrap; 
                    text-overflow: ellipsis;
                }
            }
            .routing {
                width: 100%;
                line-height: 40px;
                padding-left: 10px;
                border-top: 2px solid #aaa;
                position: absolute;
                top: 80px;
                .ant-breadcrumb {
                    margin-top: 7px;
                }
            }
        }
        .content {
            width: 100%;
            background: #eee;
            position: absolute;
            top: 123px;
            bottom: 0;
            right: 0;
            padding: 10px 10px 0 266px;
            overflow-y: scroll;
            &::-webkit-scrollbar {
                display: none;
            }
            &::-moz-scrollbar {
                display: none;
            }
            &::-ms-scrollbar {
                display: none;
            }
        }
    }
}